import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { IngredientComponent } from './ingredient.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ScrollingModule } from '@angular/cdk/scrolling';
const routes: Routes = [
  {
    path: '',
    component:  IngredientComponent
  },
  {
    path: 'purchase',
    loadChildren: () => import('./ingredient-in/ingredient-in.module').then(m => m.IngredientInModule)
  },
  {
    path: 'issues',
    loadChildren: () => import('./ingredient-out/ingredient-out.module').then(m => m.IngredientOutModule)
  },
  {
    path: 'return',
    loadChildren: () => import('./ingredient-return/ingredient-return.module').then(m => m.IngredientReturnModule)
  },
  {
    path: 'enter',
    loadChildren: () => import('./kitchen-return/kitchen-return.module').then(m => m.KitchenReturnModule)
  }
]

@NgModule({
  declarations: [IngredientComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    ScrollingModule
  ]
})
export class IngredientModule { }
